.form {
    margin: 20px 0;
    width: 100%;
    display: grid;
    gap: 20px;
}

.button:hover {
    cursor: pointer;
}

.wrongInput {
    border: 1px solid red;
}

.wrongInputMessage {
    color: red;
    font-style: italic;
    margin: 0;
    text-align: center;
}

.wobble {
    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    backface-visibility: hidden;
    perspective: 1000px;
}

@keyframes shake {
    10%, 90% {
      transform: translate3d(-1px, 0, 0);
    }
    
    20%, 80% {
      transform: translate3d(2px, 0, 0);
    }
  
    30%, 50%, 70% {
      transform: translate3d(-4px, 0, 0);
    }
  
    40%, 60% {
      transform: translate3d(4px, 0, 0);
    }
}